import React from 'react'
import Cover from './components/Cover'
import {
  TextField,
  List,
  ListItem,
  Select,
  InputLabel,
  FormControl,
  MenuItem,
  IconButton,
} from '@material-ui/core/'
import { Send, Delete } from '@material-ui/icons'
import { SliderPicker, ChromePicker } from 'react-color'

const App: React.FC = () => {
  const [state, dispatch] = React.useState({
    fs: 18,
    fontColor: '#fefefe',
    size: 500,
    tunes: [
      'Saint James Infirmary Blues ~ Jon Batiste',
      'Nocturne No. 1 in D Minor ~ Jon Batiste',
      'The Very Thought Of You ~ Jon Batiste',
      'Una Rosa Blanca ~ Ibrahim Maalouf',
      'Happy Face ~ Ibrahim Maalouf',
    ],
    art:
      'https://jambands.com/wp-content/uploads/2018/07/761fa2b5-4ff6-4348-a966-4f7b51429543_1000x1000.jpg',
    artSize: 310,
    tuneBuffer: '',
    background: '#2d6086',
    position: 'auto 0 auto auto',
  })

  const handleChange = (key: string) => (e: any) =>
    dispatch({ ...state, [key]: e.target.value })

  const handleItemChange = (index: number, value: string) =>
    dispatch({
      ...state,
      tunes: state.tunes.map((t, i) => (i === index ? value : t)),
    })

  const submitTune = () =>
    dispatch({
      ...state,
      tuneBuffer: '',
      tunes: [...state.tunes, state.tuneBuffer],
    })

  const deleteTune = (index: number) => () =>
    dispatch({
      ...state,
      tunes: state.tunes.filter((_, i) => i !== index),
    })

  return (
    <div className="wrapper">
      <aside className="aside">
        <form
          action=""
          onSubmit={e => {
            e.preventDefault()
          }}
          className="aside__form"
        >
          <TextField
            label="Cover size"
            value={state.size}
            onChange={handleChange('size')}
          />
          <TextField
            label="Art size"
            value={state.artSize}
            onChange={handleChange('artSize')}
            margin="normal"
          />
          <TextField
            label="Font size"
            value={state.fs}
            onChange={handleChange('fs')}
            margin="normal"
          />
          <TextField
            label="Cover art"
            value={state.art}
            onChange={handleChange('art')}
            margin="normal"
          />
          <h2 className="aside__label">cover bg:</h2>
          <SliderPicker
            color={state.background}
            onChange={c => dispatch({ ...state, background: c.hex })}
          />
          <FormControl className="aside__form__select">
            <InputLabel>Cover Art Position</InputLabel>
            <Select
              value={state.position}
              onChange={handleChange('position')}
            >
              <MenuItem value="0 0 auto auto">Top</MenuItem>
              <MenuItem value="auto 0 auto auto">Center</MenuItem>
              <MenuItem value="auto 0 0 auto">Bottom</MenuItem>
            </Select>
          </FormControl>

          <h2 className="aside__label">font color:</h2>
          <ChromePicker
            color={state.fontColor}
            onChange={c => dispatch({ ...state, fontColor: c.hex })}
          />

          {/*Items*/}
          <h2 className="aside__label">tunes:</h2>
          <List>
            {state.tunes.map((t, i) => (
              <ListItem key={i}>
                <TextField
                  fullWidth={true}
                  value={t}
                  onChange={e => handleItemChange(i, e.target.value)}
                />
                <IconButton
                  edge="end"
                  aria-label="send"
                  onClick={deleteTune(i)}
                >
                  <Delete />
                </IconButton>
              </ListItem>
            ))}
            <ListItem>
              <TextField
                onKeyPress={e => {
                  if (e.keyCode === 13) submitTune()
                }}
                fullWidth={true}
                value={state.tuneBuffer}
                onChange={handleChange('tuneBuffer')}
              />
              <IconButton edge="end" aria-label="send" onClick={submitTune}>
                <Send />
              </IconButton>
            </ListItem>
          </List>
        </form>
      </aside>
      <main className="main">
        <Cover {...state} />
      </main>
    </div>
  )
}

export default App
