import React from 'react'

interface CoverProps {
  size: number
  fs: number
  tunes: string[]
  art: string
  artSize: number
  background: string
  fontColor: string
  position: string
}

const Cover: React.FC<CoverProps> = ({
  fs,
  size,
  tunes,
  art,
  artSize,
  background,
  fontColor,
  position
}) => {
  const style = ({
    '--cover-font-s': `${fs}px`,
    '--cover-font-c': fontColor,
    '--cover-s': `${size}px`,
    '--cover-bg': background,
    '--cover-art-pos': position,
    '--cover-art-s': `${artSize}px`,
  } as any) as React.CSSProperties

  return (
    <div className="cover" style={style}>
      <ol className="cover__tunes">
        {tunes.map((t, i) => (
          <li className="cover__tunes__item" key={i}>
            {t}
          </li>
        ))}
      </ol>

      <div className="cover__art">
        <img
          width={artSize}
          height={artSize}
          src={art}
          alt=""
          className="cover__art__img"
        />
      </div>
    </div>
  )
}

export default Cover
